
.feedback__container {
  @media (min-width: $vp-1024) {
    position: relative;
    background: url("../img/triangle.png") no-repeat 0% 98% $deep-sky-blue;

    @media (min-resolution: 2dppx) {
      background: url("../img/triangle@2x.png") no-repeat 0% 99% $deep-sky-blue;
    }
  }

  @media (max-width: $vp-1023) {
    background: $deep-sky-blue;
  }
}

.feedback__wrapper {
  @media (min-width: $vp-1024) {
    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    justify-content: end;
    position: relative;
    background: url("../img/form-plane.png") no-repeat 5% 247px;
    background-size: 39%;

    @media (min-resolution: 2dppx) {
      background: url("../img/form-plane@2x.png") no-repeat 5% 247px;
      background-size: 39%;
    }
  }

  @media (max-width: $vp-1023) {
    justify-content: center;
    background-image: none;
  }
}

.feedback__content-container {
  display: flex;
  flex-direction: column;
  min-width: 50%;
  padding-top: 134px;
  padding-bottom: 203px;
  padding-right: 15px;

  @media (max-width: $vp-1023) {
    width: 100%;
    padding-top: 13px;
    padding-bottom: 70px;
    padding-right: 0;
  }

  @media (max-width: $vp-767) {
    padding-bottom: 62px;
  }
}

.feedback__title-container {
  padding-top: 31px;
  padding-left: 30px;

  @media (max-width: $vp-1023) {
    padding-left: 49px;
  }

  @media (max-width: $vp-767) {
    padding-left: 25px;
    padding-top: 34px;
  }
}

.feedback__title-container h2 {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin: 0;

  @media (max-width: $vp-767) {
    font-size: 30px;
    line-height: 42px;
  }
}

.feedback__title-container p {
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: $color-default-white;
  margin: 0;
  padding-top: 12px;
  padding-left: 6px;

  @media (max-width: $vp-1023) {
    padding-top: 9px;
    padding-left: 1px;
  }

  @media (max-width: $vp-767) {
    font-size: 14px;
    line-height: 19px;
    padding-top: 8px;
    padding-right: 32px;
  }
}

.feedback__form-container ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 52% 48%;
  grid-template-areas:
    "name phone"
    "text text";

  @media (max-width: $vp-767) {
    display: flex;
    flex-direction: column;
  }
}

.feedback__content-container li {
  margin-bottom: 23px;

  @media (max-width: $vp-1023) {
    margin-bottom: 21px;
  }
}

.feedback__form-container fieldset {
  border: none;
  padding: 0;
}

.feedback__form-container fieldset ~ div {
  padding-top: 5px;
  display: grid;
  grid-template-columns: 45.4% 54%;

  @media (max-width: $vp-1023) {
    grid-template-columns: 40% 54%;
    padding-top: 8px;
  }

  @media (max-width: $vp-767) {
    display: flex;
    flex-direction: column-reverse;
    padding-top: 5px;
  }
}

.feedback__form-container form {
  padding: 5.6% 5.1% 7% 5%;

  @media (max-width: $vp-1023) {
    padding: 3.1% 6.5% 7% 6.2%;
  }

  @media (max-width: $vp-767) {
    padding: 10.1% 7.5% 7% 7.2%;
  }
}

.feedback__string-wrapper input,
textarea {
  font-family: "PTMono", "Arial", sans-serif;
  width: 100%;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  background-color: $shade-black-pearl;
  border: 0;
  box-sizing: border-box;
  height: 49px;
  padding: 0 0 0 15px;
  color: $color-default-white;
  resize: none;
}

.feedback__string-wrapper input::-moz-placeholder {
  color: $color-default-white;
}

.feedback__string-wrapper textarea::-moz-placeholder {
  color: $color-default-white;
}

.feedback__string-wrapper input::-webkit-input-placeholder {
  color: $color-default-white;
}

.feedback__string-wrapper textarea::-webkit-input-placeholder {
  color: $color-default-white;
}

.feedback__string-wrapper textarea {
  height: 156px;
  padding: 21px 0 0 14px;

  @media (max-width: $vp-1023) {
    padding: 31px 0 0 14px;
  }

  @media (max-width: $vp-767) {
    padding: 23px 0 0 14px;
    height: 180px;
  }
}

.feedback__form-container label {
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $shade-white;
  cursor: pointer;
  position: relative;
}

.feedback__form-container label:hover {
  color: $black-pearl;
}

.feedback__checkbox-wrapper {
  display: flex;
}

.feedback__checkbox-container {
  padding-left: 11%;
  padding-top: 4.3%;
  position: relative;
  display: grid;
  grid-template-columns: 30px 1fr;
  column-gap: 14px;
  grid-column: 2;
  grid-row: 1;

  @media (max-width: $vp-1023) {
    padding-left: 18%;
    padding-top: 3.3%;
    padding-bottom: 2px;
    padding-right: 10%;
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 23px 1fr;
    padding-left: 0;
    padding-top: 9.3%;
    column-gap: 10px;
  }
}

.feedback__form-container li:nth-of-type(2n+1) {
  grid-area: name;
  margin-right: 30px;

  @media (max-width: $vp-767) {
    margin-right: 0;
  }
}

.feedback__form-container li:nth-of-type(3n+2) {
  grid-area: phone;
}

.feedback__form-container li:nth-of-type(5n-2) {
  grid-area: text;
  margin-right: 0;
}

.feedback__checkbox-container input {
  opacity: 0;
}

.feedback__checkbox-container input + label::before {
  content: "";
  position: absolute;
  left: -28px;
  top: 8px;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid $color-default-white;
  border-radius: 2px;
  box-sizing: border-box;
  z-index: 1;

  @media (max-width: $vp-1023) {
    left: -25px;
    top: 7px;
  }

  @media (max-width: $vp-767) {
    left: -26px;
    top: 6px;
  }
}

.feedback__checkbox-container input + label::after {
  content: "";
  position: absolute;
  left: -25px;
  top: 10px;
  width: 8px;
  height: 7px;
  box-sizing: border-box;
  border-left: 2px solid $color-default-white;
  border-bottom: 2px solid $color-default-white;
  transform: rotate(-45deg);
  z-index: 3;
  display: none;

  @media (max-width: $vp-1023) {
    left: -22px;
    top: 10px;
    width: 6px;
    height: 6px;
  }

  @media (max-width: $vp-767) {
    left: -22px;
    top: 9px;
  }
}

.feedback__checkbox-container input:checked + label::after {
  content: "";
  position: absolute;
  left: -25px;
  top: 10px;
  width: 8px;
  height: 7px;
  box-sizing: border-box;
  border-left: 2px solid $color-default-white;
  border-bottom: 2px solid $color-default-white;
  transform: rotate(-45deg);
  z-index: 3;
  display: block;

  @media (max-width: $vp-1023) {
    left: -22px;
    top: 10px;
    width: 6px;
    height: 6px;
  }

  @media (max-width: $vp-767) {
    left: -22px;
    top: 9px;
  }
}

.feedback__checkbox-container input:hover + label::before {
  border: 2px solid $black-pearl;
  cursor: pointer;
}

.feedback__checkbox-container input:focus + label::before {
  border: 2px solid $black-pearl;
  cursor: pointer;
}

.feedback__wrapper button {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid transparent;
  border-radius: 3px;
  text-decoration: none;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 2% 12%;
  color: $color-default-white;
  background-color: $black-pearl;
  cursor: pointer;
  width: 100%;
  grid-column: 1;
  grid-row: 1;
  margin-left: 3px;

  @media (max-width: $vp-1023) {
    padding: 6% 12% 3%;
    margin-left: 0;
  }

  @media (max-width: $vp-767) {
    padding: 5% 12% 4%;
  }
}

.feedback__wrapper button:hover {
  color: $black-pearl;
  background-color: $iron;
  outline: 2px solid $black-pearl;
}

.feedback__wrapper button:focus {
  color: $black-pearl;
  background-color: $iron;
  outline: 2px solid $black-pearl;
}

.feedback__wrapper button:active {
  color: $black-pearl;
  background-color: $iron;
  outline: 2px solid $black-pearl;
  transform: scale(1.1);
}
