.services-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 53px;
  padding-left: 0;

  @media (max-width: $vp-1200) {
    justify-content: center;
  }

  @media (max-width: $vp-1023) {
    padding: 39px 0 0;
  }

  @media (max-width: $vp-767) {
    padding: 30px 0 56px;
  }
}

.services-list__item {
  margin: 0 19px 30px;
  width: 27.8%;


  @media (max-width: $vp-1023) {
    width: 92%;
  }

  @media (max-width: $vp-767) {
    width: 100%;
    margin: 0 5px 30px;
  }
}
